listItems.forEach((item) => {
  item.addEventListener("click", function () {
    var pgname = this.getAttribute("value");
    var pgclick = document.getElementById(pgname);
    if (pgclick !== null) {
      listItems.forEach((li) => {
        // X√≥a class 'selected' kh·ªèi t·∫•t c·∫£ c√°c m·ª•c <li> kh√°c
        li.classList.remove("active");
        var pgname = li.getAttribute("value");
        var pg = document.getElementById(pgname);
        try {
          pg.classList.add("hidden");
        } catch {}
      });
      // Th√™m class 'selected' cho m·ª•c <li> v·ª´a nh·∫•p v√†o
      pgclick.classList.remove("hidden");
      this.classList.add("active");
      if (pgname === "pgdangky") {
        fetchDs();
      }
    }
  });
});

function fetchDs() {
  if (dsns.length === 0) {
    let submitData = {
      type: "dangky"
    };
    console.log("ƒëang l·∫•y danh s√°ch nh√¢n s·ª±");
    modal.classList.add("display");
    fetch(URL, {
      method: "POST",
      headers: {
        "Content-Type": "text/plain;charset=utf-8"
      },
      body: JSON.stringify(submitData) // p data type must match "Content-Type" header
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        dsns = [...data];
        modal.classList.remove("display");
      })
      .catch((error) => {
        console.error("Error:", error);
        modal.classList.remove("display");
        alert("Kh√¥ng t√¨m th·∫•y c∆° s·ªü d·ªØ li·ªáu nh√¢n s·ª±, vui l√≤ng ki·ªÉm tra l·∫°i");
      });
  }
}

btnThemNg.addEventListener("click", (e) => {
  e.preventDefault();
  var donvi = document.querySelectorAll(".DVcongTac");
  var dvi = donvi[donvi.length - 1].value;
  stt += 1;
  const temporaryDiv = document.createElement("div");

  var innerEle = `<input style="width:18px;border: none;padding-top:10px" value="${stt}/" class="soTT Ng${stt}" readonly></input>
  <input class="soCCCD Ng${stt}" type="text" style="width:108px; margin-bottom:2px" placeholder="S·ªë CCCD"></input>
  <input class="HoVaTen Ng${stt}" type="text" style="width:210px; margin-bottom:2px" placeholder="H·ªç v√† t√™n"></input>
  <button id= "Ng${stt}" class="btnXoa Ng${stt}" style="width:21px;background-color:red;font-weight: ;">X</button>
  <br class="Ng${stt}">
  <input class="SoDT Ng${stt}" type="text" style="width:108px; margin-bottom:2px;margin-left: 25px" placeholder="S·ªë ƒëi·ªán tho·∫°i"></input>
  <input class="DVcongTac Ng${stt}" type="text" style="width:235px; margin-bottom:2px" placeholder="ƒê∆°n v·ªã c√¥ng t√°c" Value="${dvi}"></input>
  <br class="Ng${stt}">`;
  temporaryDiv.innerHTML = innerEle;
  while (temporaryDiv.firstChild) {
    dsnguoivao.appendChild(temporaryDiv.firstChild);
  }
  var dsbtnXoa = document.querySelectorAll(".btnXoa");
  lay_lai_tt();
  dsbtnXoa.forEach((btnXoa) => {
    btnXoa.addEventListener("click", (e) => {
      e.preventDefault();
      var tt = btnXoa.getAttribute("id");
      var dsinput = document.querySelectorAll(`.${tt}`);
      dsinput.forEach((elem) => {
        dsnguoivao.removeChild(elem);
        lay_lai_tt();
      });
    });
  });
});

function lay_lai_tt() {
  var dsSoTT = document.querySelectorAll(".soTT");
  for (var i = 0; i < dsSoTT.length; i++) {
    dsSoTT[i].value = `${i + 1}/`;
  }
}

idnguoidk.addEventListener("input", (e) => {
  idnguoidk.value = idnguoidk.value.toUpperCase();
  // idquanly.value = "";
  if (idnguoidk.value.length >= 7) {
    const nv = dsns.find(
      (nv) => nv.MaNV.toLowerCase() === idnguoidk.value.toLowerCase()
    );
    if (!nv) {
      idhotendk.value = "kh√¥ng t√¨m th·∫•y";
      idhotendk.classList.add("fal");
      idhotendk.classList.remove("done");
      idbophan.value = "";
      idchucvu.value = "";
    } else {
      idhotendk.classList.remove("fal");
      idhotendk.classList.add("done");
      idhotendk.value = nv.HoTen;
      idbophan.value = nv.BoPhan;
      idchucvu.value = nv.ChucVu;
      var dsql = [];
      dsql = dsns.filter(
        (rs) =>
          // rs.BoPhan.toLowerCase() === idbophan.value.toLowerCase() &&
          rs.CapDuyet.toLowerCase() === "tr∆∞·ªüng b·ªô ph·∫≠n" ||
          rs.CapDuyet.toLowerCase() === "ph√≥ ph√≤ng"
      );

      if (dsql.length > 0) {
        // var innerSelect = `<label>Qu·∫£n l√Ω x√°c nh·∫≠n:</label>
        // <select id="quanly" style="font-size:16px; height: 25px;">`;
        var innerSelect = `<option value="">- Ch·ªçn qu·∫£n l√Ω x√°c nh·∫≠n -</option>`;
        dsql.forEach(
          (rs) =>
            (innerSelect += `<option value ="${rs.HoTen}|${rs.BoPhan}">[${rs.BoPhan}] ${rs.HoTen}</option>`)
        );
        // innerSelect += `</select>`;
        idquanly.innerHTML = innerSelect;
      } else {
        idquanly.innerHTML = "";
      }
    }
  } else {
    idhotendk.value = "";
    idbophan.value = "";
    idchucvu.value = "";
  }
});

btnguidk.addEventListener("click", (e) => {
  e.preventDefault();
  var cccd = document.querySelectorAll(".soCCCD");
  var hoten = document.querySelectorAll(".HoVaTen");
  var dt = document.querySelectorAll(".SoDT");
  var donvi = document.querySelectorAll(".DVcongTac");
  var dsdangky = [];
  var checkinput = "true";
  for (var i = 0; i < cccd.length; i++) {
    var socccd = cccd[i].value;
    var hovaten = hoten[i].value;
    var sodt = dt[i].value;
    var dvcongtac = donvi[i].value;
    if (socccd === "") {
      cccd[i].classList.add("thieudl");
      checkinput = "false";
    } else {
      cccd[i].classList.remove("thieudl");
    }
    if (hovaten === "") {
      hoten[i].classList.add("thieudl");
      checkinput = "false";
    } else {
      hoten[i].classList.remove("thieudl");
    }
    if (sodt === "") {
      dt[i].classList.add("thieudl");
      checkinput = "false";
    } else {
      dt[i].classList.remove("thieudl");
    }
    if (dvcongtac === "") {
      donvi[i].classList.add("thieudl");
      checkinput = "false";
    } else {
      donvi[i].classList.remove("thieudl");
    }
    dsdangky.push(`${socccd} | ${hovaten} | ${sodt} | ${dvcongtac}`);
  }
  if (checkinput === "false") {
    alert("Vui l√≤ng nh·∫≠p ƒë·∫ßy ƒë·ªß th√¥ng tin ng∆∞·ªùi v√†o c√¥ng ty");
    return;
  }
  const MaNV = idnguoidk.value;
  const HoTen = idhotendk.value;
  const BoPhan = idbophan.value;
  const ChucVu = idchucvu.value;
  const TuNgay = idtungay.value;
  const DenNgay = iddenngay.value;
  const TuGio = idtuthgian.value;
  const DenGio = iddenthgian.value;
  const LyDo = idlydo.value;
  const GhiChu = idghichu.value;
  const QuanLy = idquanly.value;
  if (MaNV === "") {
    alert("Vui l√≤ng nh·∫≠p m√£ nh√¢n vi√™n");
    return;
  }
  if (HoTen === "" || HoTen === "kh√¥ng t√¨m th·∫•y") {
    alert("M√£ nh√¢n vi√™n ch∆∞a ƒë√∫ng, vui l√≤ng ki·ªÉm tra l·∫°i");
    return;
  }
  if (TuNgay === "" || DenNgay === "" || TuGio === "" || DenGio === "") {
    alert("Vui l√≤ng nh·∫≠p ƒë·∫ßy ƒë·ªß ng√†y th√°ng, th·ªùi gian d·ª± ki·∫øn v√†o c√¥ng ty");
    return;
  }
  if (TuNgay > DenNgay) {
    alert(
      "Ng√†y th√°ng kh√¥ng h·ª£p l·ªá! Ng√†y 'T·ª´ ng√†y' kh√¥ng th·ªÉ l·ªõn h∆°n 'ƒê·∫øn ng√†y'"
    );
    return;
  }
  if (LyDo === "") {
    alert("Vui l√≤ng nh·∫≠p l√Ω do cho ng∆∞·ªùi v√†o c√¥ng ty");
    return;
  }
  if (QuanLy === "") {
    alert("Vui l√≤ng ch·ªçn qu·∫£n l√Ω x√°c nh·∫≠n");
    return;
  }
  let ngaythang = TuNgay.split("-");
  var qs = confirm(
    `X√ÅC NH·∫¨N! \nG·ª≠i ƒëƒÉng k√Ω cho ng∆∞·ªùi v√†o c√¥ng ty ng√†y ${ngaythang[2]}/${ngaythang[1]}/${ngaythang[0]}`
  );
  if (qs === true) {
    const type = "dangkynguoivao";
    const data = {
      MaNV,
      HoTen,
      BoPhan,
      ChucVu,
      dsdangky,
      LyDo,
      TuNgay,
      DenNgay,
      TuGio,
      DenGio,
      GhiChu,
      QuanLy
    };
    const submitData = { type, data };
    modal.style.display = "flex";
    fetch(URL, {
      method: "POST",
      headers: {
        "Content-Type": "text/plain;charset=utf-8"
      },
      body: JSON.stringify(submitData) // p data type must match "Content-Type" header
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        console.log(data);
        if (data === true) {
          alert(
            "‚úÖ ƒêƒÉng k√Ω th√†nh c√¥ng! Vui l√≤ng li√™n h·ªá v·ªõi qu·∫£n l√Ω ƒë·ªÉ ƒë∆∞·ª£c x√°c nh·∫≠n"
          );
        } else {
          alert("‚ùå ƒêƒÉng k√Ω kh√¥ng th√†nh c√¥ng ‚ö† Vui l√≤ng th·ª≠ l·∫°i");
        }
      })
      .catch((error) => {
        console.error("Error:", error);
        alert("‚ùå ƒêƒÉng k√Ω kh√¥ng th√†nh c√¥ng ‚ö† Vui l√≤ng th·ª≠ l·∫°i");
      });
    modal.style.display = "none";
  }
});

